import React from 'react';
import { Row, Col, Button } from 'antd';
import * as sdk from '@seatalk/web-app-sdk';

const BasicEntry: React.FC = () => {
  return (
    <>
      <h2>sdk-basic</h2>
      <Row>
        <Col span={12}>
          <Button
            type="primary"
            onClick={() => {
              sdk.getSSOToken({
                onSuccess: (token: string) => {
                  console.log(
                    'SSO token:',
                    new Date().toLocaleTimeString(),
                    token
                  );
                },
              });
            }}
          >
            getSSOToken
          </Button>
        </Col>

        <Col span={12}>
          <Button
            type="primary"
            onClick={() => {
              setTimeout(() => {
                sdk.getSSOToken({
                  onSuccess: (token: string) => {
                    console.log(
                      'SSO token (3s delay):',
                      new Date().toLocaleTimeString(),
                      token
                    );
                  },
                });
              }, 3e3);
            }}
          >
            getSSOToken (3s delay)
          </Button>
        </Col>
      </Row>

      <Row>
        <Col span={12}>
          <Button
            type="primary"
            onClick={() => {
              sdk.navigateBack({ data: 'Data from Web APP', delta: 1 });
            }}
          >
            navigateBack
          </Button>
        </Col>
      </Row>
    </>
  );
};

export default BasicEntry;
