import React, { useEffect, useState, createRef } from 'react';
import { Form, Button, Input, Switch } from 'antd';

import { Error } from '@seatalk/web-app-sdk/lib/types';
import * as sdk from '@seatalk/web-app-sdk';

import styles from './style.module.css';

const UserEntry: React.FC = () => {
  const [hasProfileCard, setHasProfileCard] = useState(false);

  const [employeeCode, setEmployeeCode] = useState('35938');
  // 是否启用targetRect参数
  const [targetRectEnabled, setTargetRectEnabled] = useState(true);
  const dragElementRef = createRef<HTMLElement>();

  const [width, setWidth] = useState(50);
  const [height, setHeight] = useState(50);

  const toggleTargetRectHandler = (v: boolean) => {
    setTargetRectEnabled(!!v);
  };

  const openUserProfileHandler = () => {
    if (!employeeCode) {
      console.log('Please input a valid employee code');
      return;
    }
    const btn = document.querySelector('.open-button');
    if (!btn) return;
    let targetRect;
    const rect = btn.getBoundingClientRect();

    if (targetRectEnabled) {
      targetRect = rect;
    } else {
      targetRect = undefined;
    }
    console.log('opeUserProfile targetRect :>> ', JSON.stringify(targetRect));

    sdk.openUserProfile({
      id: employeeCode,
      idType: 'employee_code',
      targetRect: targetRect,
      onSuccess: () => {
        console.log('openUserProfile success!');
        setHasProfileCard(true);
      },
      onError: (e: Error) => {
        console.log('openUserProfile failed! ', e.code, e.message);
      },
    });
    return;
  };

  useEffect(() => {
    let x0: number;
    let y0: number;
    let capture = false;
    const drag: HTMLElement | null = dragElementRef.current;

    // 拖动事件
    if (drag) {
      const fn0 = (e: any) => {
        x0 = e.clientX - drag.offsetLeft;
        y0 = e.clientY - drag.offsetTop;
        capture = true;
      };
      drag.addEventListener('mousedown', fn0, false);

      const fn1 = (e: any) => {
        if (capture) {
          drag.style.left = e.clientX - x0 + 'px';
          drag.style.top = e.clientY - y0 + 'px';
        }
      };

      document.addEventListener('mousemove', fn1, false);

      const fn2 = () => {
        capture = false;
      };

      document.addEventListener('mouseup', fn2, false);
    }

    // 滚动事件
    const root = document.querySelector('#root');
    if (root) {
      root.addEventListener('scroll', () => {
        console.log('scroll');
        if (hasProfileCard) {
          // removeUserProfileHandler();
        }
      });
    }
  }, []);

  return (
    <>
      <div className={styles.page}>
        <div className={styles.form}>
          <Form.Item label="Employee Code">
            <Input
              value={employeeCode}
              onChange={(e) => {
                setEmployeeCode(e.target.value);
              }}
              placeholder="Please input a employee code"
            />
          </Form.Item>

          <Form.Item label="Enable targetRect Params?">
            <Switch
              style={{ marginLeft: 20 }}
              checkedChildren="Enable"
              unCheckedChildren="Disable"
              defaultChecked
              onChange={toggleTargetRectHandler}
            ></Switch>
          </Form.Item>

          <Form.Item label="Avatar Width">
            <Input
              value={width}
              type="number"
              onChange={(e) => {
                const v = +e.target.value;
                setWidth(v);
              }}
              placeholder="width of avatar"
            />
          </Form.Item>

          <Form.Item label="Avatar Height">
            <Input
              value={height}
              type="number"
              onChange={(e) => {
                const v = +e.target.value;
                setHeight(v);
              }}
              placeholder="height of avatar"
            />
          </Form.Item>
        </div>

        <Button
          className={styles.openButton}
          style={{ width: width, height: height }}
          type="primary"
          ref={dragElementRef}
          onClick={openUserProfileHandler}
        >
          <span
            style={{
              position: 'absolute',
              left: 0,
              bottom: -30,
              color: 'black',
            }}
          >
            openUserProfile
          </span>
          <div className={styles.dot}></div>
        </Button>
      </div>
    </>
  );
};

export default UserEntry;
