import React from 'react';
import { Row, Col, Button } from 'antd';
import * as sdk from '@seatalk/web-app-sdk';

const ConfigEntry: React.FC = () => {
  const getSystemInfoHandler = () => {
    console.log('screenWidth: ', window.screen.width);
    console.log('screenHeight: ', window.screen.height);
    console.log('windowWidth: ', window.innerWidth);
    console.log('windowHeight: ', window.innerHeight);
    console.log('devicePixelRatio', window.devicePixelRatio);
    sdk.getSystemInfo({
      onSuccess: (data: any) => {
        console.log('getSystemInfo success: ', data);
      },
      onError: (e: any) => {
        console.log('getSystemInfo error: ', e);
      },
    });
  };

  return (
    <>
      <h2>sdk-config</h2>
      <Row>
        <Col span={12}>
          <Button type="primary" onClick={getSystemInfoHandler}>
            getSystemInfo
          </Button>
        </Col>
      </Row>
    </>
  );
};

export default ConfigEntry;
