import React from 'react';
import { Button, Form, Input, Switch, Tag } from 'antd';
import * as sdk from '@seatalk/web-app-sdk';
import { Error } from '@seatalk/web-app-sdk/lib/types';

let encode = true;

interface Props {
  title: string;
  subtitle: string;
  imageUri: string;
  mobilePath: string;
  desktopPath: string;
  mobileType: string;
  desktopType: string;
  mobileParams: string;
  desktopParams: string;
}

function shareAppHandler(values: Props) {
  // console.log('Form Values :>> ', values);

  values.mobilePath = values.mobilePath || '';
  values.desktopPath = values.desktopPath || '';

  const shareParams: Record<string, string | unknown> = {
    title: values.title,
    subtitle: values.subtitle,
    imageUri: values.imageUri,
    onSuccess: () => sdk.toast({ message: 'onSuccess' }),
    onCancel: () => sdk.toast({ message: 'onCancel' }),
    onError: (e: Error) => {
      const message = `${e.code}-${e.message || 'can not share app.'}`;
      sdk.toast({ message });
    },
  };

  if (values.mobileType === 'rn' || values.mobileType === 'web') {
    const params = JSON.parse(values.mobileParams || '{}');
    const encodedParams = {};
    Object.entries(params).forEach((entry: [string, unknown]) => {
      const [key, value] = entry;
      encodedParams[key] = encodeURI(value as string);
    });

    shareParams.mobileLink = {
      type: values.mobileType,
      path: encode ? encodeURI(values.mobilePath) : values.mobilePath,
      params: encode ? encodedParams : params,
    };
  }
  if (values.desktopType === 'rn' || values.desktopType === 'web') {
    const params = JSON.parse(values.desktopParams || '{}');
    const encodedParams = {};
    Object.entries(params).forEach((entry: [string, unknown]) => {
      const [key, value] = entry;
      encodedParams[key] = encodeURI(value as string);
    });
    shareParams.desktopLink = {
      type: values.desktopType,
      path: encode ? encodeURI(values.desktopPath) : values.desktopPath,
      params: encode ? encodedParams : params,
    };
  }

  sdk.shareApp({
    ...shareParams,
  });
}

function shareAppEncodeChangeHandler(value: boolean) {
  encode = value;
}

const ShareApp: React.FC = () => {
  return (
    <>
      <Form
        name="basic"
        layout="vertical"
        labelCol={{ offset: 0, span: 24 }}
        wrapperCol={{ span: 24 }}
        autoComplete="off"
        onFinish={shareAppHandler}
        style={{ padding: 20 }}
      >
        <Form.Item
          label="Title"
          name="title"
          initialValue={'Seatalk Web Demo App'}
        >
          <Input placeholder="share title" />
        </Form.Item>
        <Form.Item
          label="Subtitle"
          name="subtitle"
          initialValue={'This is a share message from web app.'}
        >
          <Input placeholder="share subtitle" />
        </Form.Item>
        <Form.Item
          label="Image"
          name="imageUri"
          initialValue={'https://seatalk.io/img/seatalk-web-logo.png'}
        >
          <Input placeholder="share image url" />
        </Form.Item>
        <p>
          <Tag color="red">Mobile Link</Tag>
        </p>
        <Form.Item label="Type" name="mobileType">
          <Input placeholder="web | rn" />
        </Form.Item>
        <Form.Item label="Path" name="mobilePath">
          <Input placeholder="please input share path" />
        </Form.Item>
        <Form.Item
          label="Params"
          name="mobileParams"
          initialValue={'{"id":"123"}'}
        >
          <Input placeholder='{"id":"123"}' />
        </Form.Item>
        <p>
          <Tag color="red">Desktop Link</Tag>
        </p>
        <Form.Item label="Type" name="desktopType" initialValue={'web'}>
          <Input placeholder="web" />
        </Form.Item>
        <Form.Item label="Path" name="desktopPath">
          <Input placeholder="please input share path" />
        </Form.Item>
        <Form.Item
          label="Params"
          name="desktopParams"
          initialValue={'{"id":"123"}'}
        >
          <Input placeholder='{"id":"123"}' />
        </Form.Item>
        <p>Path 和 Params 会通过 encodeURI() 进行编码</p>

        <Form.Item>
          <Switch
            checkedChildren="Encode"
            unCheckedChildren="No Encode"
            defaultChecked
            onChange={shareAppEncodeChangeHandler}
          ></Switch>
        </Form.Item>

        <Form.Item wrapperCol={{ span: 16 }}>
          <Button type="primary" htmlType="submit">
            Share App
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};

export default ShareApp;
