/* eslint-disable */
import React, { useState, useEffect, useRef } from 'react';
import { PageHeader, Button, Space, Divider, Switch, Tag } from 'antd';
import { Console, Hook, Unhook } from 'console-feed';
import { Message } from 'console-feed/lib/definitions/Console';
import { Resizable } from 're-resizable';
import { Outlet, useLocation } from 'react-router-dom';

import * as sdk from '@seatalk/web-app-sdk';

import styles from './style.module.css';

const Layout: React.FC = () => {
  const [showConsole, setShowConsole] = useState(true);
  const [logs, setLogs] = useState<Message[]>([]);
  const consoleRef = useRef<HTMLDivElement>(null);
  const location = useLocation();

  useEffect(() => {
    Hook(
      window.console,
      (log) => setLogs((currLogs) => [...currLogs, log]),
      false
    );
    console.log('window.location.href:', window.location.href);
    console.log('sdk.clientInfo:', sdk.clientInfo);
    return () => {
      // @ts-ignore
      Unhook(window.console);
    };
  }, []);

  useEffect(() => {
    if (consoleRef.current) {
      consoleRef.current.scrollTop = consoleRef.current.scrollHeight;
    }
    if (logs.length) {
      setShowConsole(true);
    }
  }, [logs]);

  return (
    <div className={styles.page}>
      {location.pathname === '/' ? (
        <PageHeader
          className={styles.pageHeader}
          tags={<Tag color="blue">v0.6.0</Tag>}
          backIcon={false}
          title="Seatalk Web SDK Demo"
        ></PageHeader>
      ) : (
        <PageHeader
          className={styles.pageHeader}
          tags={<Tag color="blue">v0.6.0</Tag>}
          onBack={() => window.history.back()}
          title="Seatalk Web SDK Demo"
        ></PageHeader>
      )}

      <div className={styles.pageContent}>
        <div className={styles.consoleControl}>
          <Space size="small">
            <Space>
              Show Console
              <Switch checked={showConsole} onChange={setShowConsole} />
            </Space>
            <Divider type="vertical" />
            <Button
              size="small"
              onClick={() => {
                // @ts-ignore
                setLogs([]);
              }}
            >
              Clear Console
            </Button>
          </Space>
        </div>

        <Outlet></Outlet>
      </div>

      <Resizable
        enable={{
          top: true,
          right: false,
          bottom: false,
          left: false,
          topRight: false,
          bottomRight: false,
          bottomLeft: false,
          topLeft: false,
        }}
        bounds="parent"
        boundsByDirection
        defaultSize={{ width: '100%', height: '30%' }}
        minWidth="100%"
        minHeight="30%"
        style={{ display: showConsole ? 'block' : 'none' }}
        className="console"
      >
        <div ref={consoleRef} className={styles.consoleInner}>
          <Console
            styles={{
              BASE_FONT_FAMILY: `source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace`,
            }}
            variant="dark"
            // @ts-ignore
            logs={logs.filter((log) => {
              return !(
                log.method === 'debug' &&
                log.data?.[0] === '[@seatalk/web-app-sdk]' &&
                log.data?.[1] === '[BridgeWebview]'
              );
            })}
          />
        </div>
      </Resizable>
    </div>
  );
};

export default Layout;
