import React from 'react';
import { Row, Col, Button } from 'antd';
import * as sdk from '@seatalk/web-app-sdk';

const UIEntry: React.FC = () => {
  return (
    <>
      <h2>sdk-ui</h2>
      <Row>
        <Col span={12}>
          <Button
            type="primary"
            onClick={() => {
              sdk.toast({ message: 'test' });
              setTimeout(() => {
                sdk.toast({ message: 'test (delayed 3s)' });
              }, 3000);
            }}
          >
            toast
          </Button>
        </Col>

        <Col span={12}>
          <Button
            type="primary"
            onClick={() => {
              sdk.showDialog({
                title: 'Dialog Title',
                message: 'Dialog message',
                okText: 'Ok',
                cancelText: 'Cancel',
                inputConfig: {
                  placeholder: '请输入',
                  prefilledText: 'test',
                  maxInputLength: 150,
                },
                onOk: () => {
                  sdk.toast({ message: 'onOk' });
                },
                onCancel: () => {
                  sdk.toast({ message: 'onCancel' });
                },
              });
            }}
          >
            showDialog
          </Button>
        </Col>
      </Row>

      <Row>
        <Col span={12}>
          <Button
            type="primary"
            onClick={() => {
              sdk.showLoading();
              setTimeout(() => {
                sdk.hideLoading();
              }, 1500);
            }}
          >
            showLoading
          </Button>
        </Col>
      </Row>
    </>
  );
};

export default UIEntry;
