import React from 'react';
import { useRoutes } from 'react-router-dom';

import Layout from './views/Layout/Layout';
import Home from './views/Home/Home';
import ShareApp from './views/ShareApp/ShareApp';
import UserProfile from './views/UserProfile/UserProfile';
import EventEmitter from './views/EventEmitter/EventEmitter';

const Routes: React.FC = () => {
  const routes = useRoutes([
    {
      path: '/',
      element: <Layout />,
      children: [
        { index: true, element: <Home></Home> },
        { path: 'share-app', element: <ShareApp></ShareApp> },
        { path: 'user-profile', element: <UserProfile></UserProfile> },
        { path: 'event-emitter', element: <EventEmitter></EventEmitter> },
      ],
    },
  ]);
  return routes;
};

export default Routes;
