import React, { useState } from 'react';
import { Button, Form, Input } from 'antd';
import * as sdk from '@seatalk/web-app-sdk';
// import { Error } from '@seatalk/web-app-sdk/lib/types';

import styles from './style.module.css';

const EventEmitter: React.FC = () => {
  const [event, setEvent] = useState('TestEvent');
  const [emitSource, setEmitSource] = useState('MTI2ODM3NDc3NDc1');
  const [registerSource, setRegisterSource] = useState('MTI2ODM3NDc3NDc1');
  const [target, setTarget] = useState('MDU3NzEzMTA4MTk3');

  const emitEventHandler = async () => {
    const targets = target.split(',');
    sdk.emit({
      event,
      source: emitSource,
      target: targets,
      data: JSON.stringify({
        id: 123,
        name: '小明',
        message: '这是我发送的数据',
        onSuccess: () => {
          console.log(`Emit Event Success: ${event}`);
        },
      }),
    });
  };
  const listenEventHandler = () => {
    sdk.listen({
      event,
      source: registerSource,
      callback: (res: Record<string, any>) => {
        console.log('App Get Event Response :>> ', res);
      },
      onSuccess: () => {
        console.log(`Listen Event Success: ${event}`);
      },
      onError: (err) => {
        console.log('listenEventHandler err :>> ', err);
      },
    });
  };

  const openRNAppHandler = () => {
    sdk.goAppLink({
      appLinkUrl: 'seatalk://application/sop/MDU3NzEzMTA4MTk3/',
      popSelf: false,
      onSuccess: () => {
        console.log('openRNAppHandler Success');
      },
      onError: (err) => {
        console.log('openRNAppHandler err :>> ', err);
      },
    });
  };
  return (
    <>
      <div className={styles.page}>
        <hr />
        <div>Source Default Value: Web SDK DEMO</div>
        <div>Target Default Value: RN SDK v0.13.0</div>
        <hr />

        <Form className={styles.form}>
          <Form.Item label="Event Name">
            <Input
              value={event}
              onChange={(e) => {
                setEvent(e.target.value);
              }}
              placeholder="Please input event name"
            />
          </Form.Item>

          <Form.Item label="Register Source">
            <Input
              value={registerSource}
              onChange={(e) => {
                setRegisterSource(e.target.value);
              }}
              placeholder="Please input event register source"
            />
          </Form.Item>

          <Form.Item label="Emit Source">
            <Input
              value={emitSource}
              onChange={(e) => {
                setEmitSource(e.target.value);
              }}
              placeholder="Please input event emit source"
            />
          </Form.Item>

          <Form.Item label="Event Target">
            <Input
              value={target}
              onChange={(e) => {
                setTarget(e.target.value);
              }}
              placeholder="Please input event target"
            />

            <p>Input multiple targets, please split with ','</p>
          </Form.Item>
        </Form>
        <Button
          type="primary"
          style={{ marginRight: 10 }}
          onClick={emitEventHandler}
        >
          Emit Event
        </Button>
        <Button type="primary" onClick={listenEventHandler}>
          Listen Event
        </Button>

        <div style={{ marginTop: 20 }}>
          <Button type="primary" onClick={openRNAppHandler}>
            Open App: RN SDK v0.13.0
          </Button>
        </div>
      </div>
    </>
  );
};

export default EventEmitter;
